let app = window.__app__ = {
  define (name, context, index) {
    let keys = context.keys()
    // 如果后续有重名的包，则忽略不处理
    if (this.modules[name]) return
    this.modules[name] = context.bind(context, keys[0])
  },
  require (name) {
    return this.modules[name](name)
  },
  routes: (window.__app__ && window.__app__.routes) || [],
  init: (nameSpace) => {
    console.log(nameSpace)
  },
  modules: {}
}

app.define('axios', require.context('axios', true, /^.\/index\.js$/))
app.define('md5', require.context('md5', false, /^.\/md5\.js$/))



