import { get, post } from '@/utils/bizApi'
import axios from 'axios'

const isDev = process.env.NODE_ENV === 'development'
const baseURL = axios.defaults.baseURL
const isMock = baseURL ? /mock/.test(baseURL) : false

// 去除空格  type 1-所有空格  2-前后空格  3-前空格 4-后空格
export function trim(str, type) {
  switch (type) {
    case 1:return str.replace(/\s+/g, '')
    case 2:return str.replace(/(^\s*)|(\s*$)/g, '')
    case 3:return str.replace(/(^\s*)/g, '')
    case 4:return str.replace(/(\s*$)/g, '')
    default:return str
  }
}

// 验证固定长度的正整数  str-需要验证的目标  limit-限制的长度
export function isRuleNumber(str, limit) {
  let res = true
  let numList = '0123456789'
  let wrapStr = String(str)
  if (wrapStr) {
    if (limit) {
      if (wrapStr.length !== limit) {
        res = false
      }
    }

    wrapStr.split('').map(item => {
      if (!(numList.includes(item))) {
        res = false
      }
    })
  } else {
    res = false
  }
  return res
}

export const compose = function(...funcs) {
    if (funcs.length === 0) return arg => arg
    if (funcs.length === 1) return funcs[0]
    return funcs.reduce((a, b) => (...args) => a(b(...args)))
}

export const pipline = function(...funcs) {
    if (funcs.length === 0) return arg => arg
    if (funcs.length === 1) return funcs[0]
    return funcs.reduce((a, b) => (...args) => b(a(...args)))
}

export function exportAPI(apis) {
    let APIs = {}

    for (let key in apis.get) {
        APIs[key] = (params = {}, options = {}) => {
            let url = apis.get[key]
            if (isDev && !isMock) {
                url = !options.javaUrl
                        ? `/api${url}`
                        : `/javaApi${url}`
            }
            return get(
                url,
                params, {
                    noErrorTips: true,
                    ...options
                }
            )
        }
    }
    for (let key in apis.post) {
        APIs[key] = (params = {}, options = {}) => {
            let url = apis.post[key]
            if (isDev && !isMock) {
                url = !options.javaUrl
                        ? `/api${url}`
                        : `/javaApi${url}`
            }
            return post(
                url,
                params,
                {
                    noErrorTips: true,
                    ...options
                }
            )
        }
    }
    return APIs
}

export function setTitle(title) {
    document.title = title
    var mobile = navigator.userAgent.toLowerCase()
    if (/iphone|ipad|ipod/.test(mobile)) {
        var iframe = document.createElement('iframe')
        iframe.style.visibility = 'hidden'
        // 替换成站标favicon路径或者任意存在的较小的图片即可
        // iframe.setAttribute('src', '/mdu/static/logo.png')
        var iframeCallback = function() {
            setTimeout(function() {
                iframe.removeEventListener('load', iframeCallback)
                document.body.removeChild(iframe)
            }, 0)
        }
        iframe.addEventListener('load', iframeCallback)
        document.body.appendChild(iframe)
    }
}

export const Type = (params) => {
    const REGEXP = /^\[object\W([a-zA-Z]+)\]$/g;
    const waitingForReg = Object.prototype.toString.call(params);
    const handleResult = REGEXP.exec(waitingForReg);
    return handleResult[1].toLowerCase();
}

/**
 * 获取字符串长度，中文两个长度 字母数字一个长度
 * @param str
 * @returns {number}
 */
export function getStrLen(str) {
    if (!(typeof str === 'string' || str instanceof String)) {
        throw new Error('param must to be string');
    }
    let len = 0;
    for (let i = 0; i < str.length; i++) {
        let c = str.charCodeAt(i);
        if ((c >= 0x0001 && c <= 0x007e) || (c >= 0xff60 && c <= 0xff9f)) {
            len++;
        } else {
            len += 2;
        }
    }
    return len;
}

/**
 * 深拷贝（基于递归）
 * @example
 * deepCopy({name: 'zhansgan', obj: {}, arr: [2, 3]})
 * @param {object} [data = {}] 原始对象
 * @param {object} [final] 可选
 * @returns {object} 深拷贝之后的对象
 */
export function deepCopy(data, final = {}) {
    for (let key in data) {
        const keyToVal = data[key]
        const val = Object.prototype.toString.call(keyToVal).match(/^\[object\s(\w+)\]$/)[1]

        switch (val) {
            case 'Object':
                final[key] = deepCopy(keyToVal, {})
                break
            case 'Array':
                final[key] = deepCopy(keyToVal, [])
                break
            case 'RegExp':
                final[key] = new RegExp(keyToVal)
                break
            case 'Undefined':
                final[key] = undefined
                break
            case 'Null':
                final[key] = null
                break
             default:
                final[key] = keyToVal
        }
   }
   return final
}

export function download({ path, name }) {
    let a = document.createElement('a')
    a.href = path
    a.download = name
    a.click()
    a.remove()
}



