// eslint-disable-next-line no-unused-vars
import Vue from 'vue'
// eslint-disable-next-line no-unused-vars
// import { install } from 'vuex'
import { Notification, MessageBox } from 'element-ui'
import { compose } from '@/utils/utils'

export default {
  install (Vue, option) {
    /**
     * 一般用在获取数据时候使用，获取成功直接执行回调，失败会提示,获取列表数据
     * @param {该操作的名称} actionText
     * @param {该操作的http } service
     * @param {该操作成功的回调} callback
     */
    const doServer = (actionText, service, callback) => {
      service().then(res => {
        if (res.code === 0) {
          callback(res)
        } else {
          Notification({
            title: `${actionText}失败!`,
            message: res.msg,
            type: 'error',
            duration: 4000
          })
        }
      }, err => {
        var error = JSON.parse(JSON.stringify(err))
        var msg = Vue.prototype.getErrorMsg(error)
        Notification({
          title: `${actionText}失败!`,
          message: msg,
          type: 'error',
          duration: 4000
        })
      })
    }
    Vue.prototype.doServer = doServer
    /** 该方法在新增或者修改时候  ******不会弹出提示框
         * 一般用在新增和编辑时候使用，成功或者失败都有对应的提示
         * @param {该操作的名称} actionText
         * @param {该操作的http } service
         * @param {该操作成功的回调} callback
         */
    let doServerAndCallback = (actionText, service, callback) => {
      service().then(res => {
        if (res.code === 0) {
          Notification({
            title: `${actionText}成功!`,
            message: '',
            type: 'success',
            duration: 2000
          })
          callback(res)
        } else {
          Notification({
            title: `${actionText}失败!`,
            message: res.msg,
            type: 'error',
            duration: 4000
          })
        }
      }, err => {
        var error = JSON.parse(JSON.stringify(err))
        var msg = Vue.prototype.getErrorMsg(error)
        Notification({
          title: `${actionText}失败!`,
          message: msg,
          type: 'error',
          duration: 4000
        })
      })
    }
    Vue.prototype.doServerAndCallback = doServerAndCallback
    /** 该方法在新增或者修改时候 *****会弹出提示框确认
         * 一般用在新增和编辑时候使用，成功或者失败都有对应的提示
         * @param {该操作的名称} actionText
         * @param {该操作的http } service
         * @param {该操作成功的回调} callback
         */
    Vue.prototype.doServerConfirm = (actionConfirmText, actionText = '操作', service, callback) => {
      MessageBox.confirm(`${actionConfirmText}, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        service().then((res) => {
          if (res.code === 0) {
            Notification({
              title: `${actionText}成功!`,
              message: '',
              type: 'success',
              duration: 2000
            })
            callback(res)
          } else {
            Notification({
              title: `${actionText}失败!`,
              message: res.msg,
              type: 'error',
              duration: 4000
            })
          }
        }, err => {
          var error = JSON.parse(JSON.stringify(err))
          var msg = Vue.prototype.getErrorMsg(error)
          Notification({
            title: `${actionText}失败!`,
            message: msg,
            type: 'error',
            duration: 4000
          })
        })
      }).catch(() => {

      })
    }
    /**
     * 用在删除确认操作
     * @param {该操作的名称} actionText
     * @param {该操作的http } service
     * @param {该操作成功的回调} callback
     */
    Vue.prototype.deleteConfirm = (actionText = '操作', service, callback) => {
      MessageBox.confirm(`此操作将删除该选项, 是否继续?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      }).then(() => {
        service().then((res) => {
          if (res.code === 0) {
            Notification({
              title: `${actionText}成功!`,
              message: '',
              type: 'success',
              duration: 2000
            })
            callback(res)
          } else {
            Notification({
              title: `${actionText}失败!`,
              message: res.msg,
              type: 'error',
              duration: 4000
            })
          }
        }, err => {
          var error = JSON.parse(JSON.stringify(err))
          var msg = Vue.prototype.getErrorMsg(error)
          Notification({
            title: `${actionText}失败!`,
            message: msg,
            type: 'error',
            duration: 4000
          })
        })
      }).catch(() => {

      })
    }
    /**
         * 验证参数是否是简单对象,也就是从OBjec继承而来
         * @param {参数} obj
         */
    let isPlaintObj = (obj) => {
      if (!obj) {
        return false
      }
      return Object.getPrototypeOf(obj) === Object.prototype
    }
    Vue.prototype.isPlaintObj = isPlaintObj
    /**
         * 从错误的信息中提取需要提示的内容
         * @param {错误信息} err
         */
    Vue.prototype.getErrorMsg = (err) => {
      if (err.response && Vue.prototype.isPlaintObj(err.response)) {
        if (err.response.data && Vue.prototype.isPlaintObj(err.response.data)) {
          return err.response.data.message || '请检查网络或者后台接口'
        } else {
          return err.response.statusText || '请检查网络或者后台接口'
        }
      }
      return '请检查网络或者后台接口'
    }

    /**
         * @description: 公共请求方法封装
         * @param {actionText} 接口成功/失败文本提示
         * @param {service} 请求方法的回调
         * @param {showTips} 请求接口成功之后是否需要文本提示
         * @param {beforeRequest} 请求开始之前的钩子函数
         * @param {successCallback} 执行请求成功的回调 默认入参为返回的数据对象
         * @param {finallyCallback} 不管请求成功/失败都会执行的方法
         * @param {confirmModal} 在请求接口之前的会话弹层（比如删除、确认弹层）确定会发送请求 选项参考el-MessageBox
         * @return: null
         */
    Vue.prototype.__DUHTTP__ = (options = {
      actionText: '调用接口',
      service: () => {},
      confirmModal: {
        title: '提示',
        text: '',
        options: {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }
      },
      // 成功返回是否需要提示
      showTips: false,
      beforeRequest: () => {},
      successCallback: () => {},
      finallyCallback: () => {}
    }) => {
      let {
        confirmModal,
        service,
        successCallback,
        finallyCallback,
        actionText = '调用接口',
        showTips,
        beforeRequest
      } = options
      if (!service) throw new Error('必须传入Serive参数')

      function showTipsCb (res) {
        if (showTips) {
          Notification({
            title: '提示',
            message: `${actionText}成功!`,
            type: 'success',
            duration: 2000
          })
        }
        return res
      }

      function successCb (res) {
        if (successCallback) successCallback(res)
      }

      function startFetch () {
        if (beforeRequest) beforeRequest()
        return service()
          .then(res => {
            if (!res) return
            let { code, msg, status } = res
            code = code === undefined ? status : code
            if (code === 0 || code === 200) {
              compose(successCb, showTipsCb)(res)
            } else {
              Notification({
                title: `${actionText}失败!`,
                message: msg,
                type: 'error',
                duration: 4000
              })
            }
          })
          .catch(err => {
            let msg = Vue.prototype.getErrorMsg(err)

            Notification({
              title: `${actionText}失败!`,
              message: msg,
              type: 'error',
              duration: 4000
            })
            return err
          })
          .finally(() => {
            if (finallyCallback) finallyCallback()
          })
      }
      // 是否有弹窗提示
      if (confirmModal) {
        return MessageBox.confirm(
          confirmModal.text,
          confirmModal.title,
          Object.assign({
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning',
            center: true
          }, confirmModal.options)
        )
          .then(() => startFetch())
          .catch(() => {})
          .finally(() => {
            if (finallyCallback) finallyCallback()
          })
      }

      startFetch()
    }
  }
}



